import React, { useState, useContext, useEffect } from 'react';
import { Button, Card, CardHeader, CardBody, CardFooter, Col, Container, Form, FormGroup, Label, FormFeedback, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import firebase from '../../../firebase/Firebase';
import Spinner from '../../../components/spinner/Spinner';
import logo from '../../../assets/img/brand/logo.png';
import { UserContext } from '../../../context/contexts';
import { Redirect } from 'react-router-dom';


function Register() {

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password1, setPassword1] = useState('');
  const [password2, setPassword2] = useState('');

  const [submitted, setSubmitted] = useState(false);
  const [registered, setregistered] = useState(false);
  const [processing, setProcessing] = useState(false);

  const { uid, emailVerified } = useContext(UserContext);

  const handleSubmit = event => {
    event.preventDefault();
    setSubmitted(true);
    if (password1 == password2 && password1.length > 5 && /\S+@\S+\.\S+/.test(email) && name.length > 2) {
      setProcessing(true);
      firebase
        .auth()
        .createUserWithEmailAndPassword(email, password1)
        .then(userCredential => {
          userCredential.user.updateProfile({ displayName: name });
          userCredential.user.sendEmailVerification();
          setregistered(true);
        }).catch(error => {
          alert(error.message);
        })
    }
  }


  return (

    <React.Fragment>
      {emailVerified ?
        <Redirect to='/market' />
        :
        null
      }
      {
        uid && !emailVerified ?
          <Redirect to='/verify' />
          :
          null
      }

      {registered ? <Container>
        <Row className="justify-content-center">
          <Col className="mt-4" md="9" lg="7" xl="6">
            <CardHeader>
              <img src={logo} />
            </CardHeader>
            <CardBody className="p-4 bg-white">
              <h1>{name}</h1>

              <p>Your account is ready.  Please check your email <strong>{email}</strong> to verify your account.</p>
              <small>Also check on spam folder if email cannot be found.</small>

            </CardBody>
            <CardFooter>
              <strong>Now you can enjoy all the Personal Account Features.</strong>
            </CardFooter>
          </Col>
        </Row>

      </Container>
        :
        <Container>
          <Row className="justify-content-center">
            <Col className="mt-5" md="9" lg="7" xl="6">
              <Card className="mx-4">
                <CardHeader>
                  <img src={logo} />
                </CardHeader>
                <CardBody className="p-4">
                  <Form onSubmit={handleSubmit}>
                    <h1>Register</h1>
                    <p className="text-muted">Create your account</p>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-user"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input invalid={submitted && name.length < 3 ? true : false} valid={name.length > 2 ? true : false} onChange={e => setName(e.target.value)} type="text" placeholder="Full Name" />
                      <FormFeedback>Name is required</FormFeedback>
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>@</InputGroupText>
                      </InputGroupAddon>
                      <Input invalid={!(/\S+@\S+\.\S+/.test(email)) && submitted ? true : false} valid={/\S+@\S+\.\S+/.test(email) ? true : false} onChange={e => setEmail(e.target.value)} type="text" placeholder="Email" />
                      <FormFeedback>Valid email address is required</FormFeedback>
                    </InputGroup>
                    <InputGroup id="password1" className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input invalid={submitted && password1.length < 6 ? true : false} valid={password1.length > 5 ? true : false} onChange={e => setPassword1(e.target.value)} type="password" placeholder="Password" />
                      <FormFeedback>Password Length must be greater than 6</FormFeedback>
                    </InputGroup>
                    <InputGroup className="mb-4">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input invalid={submitted && password1 != password2 ? true : false} valid={password2.length > 5 && password1 == password2 ? true : false} onChange={e => setPassword2(e.target.value)} type="password" placeholder="Repeat password" />
                      <FormFeedback>Password doesn't match</FormFeedback>
                    </InputGroup>
                    <Button color="success" block>{processing ? "Creating Your Account ..." : "Create Account"}</Button>
                  </Form>
                </CardBody>
                <CardFooter className="p-4">
                  {processing ?
                    <div className="text-center">
                      <Spinner className="text-center" />
                    </div>
                    :
                    <small>By registering account you agree to our <a target="_blank" href="https://users.nepsechart.com/policy/privacyPolicy.html">Privacy Policy</a> and <a target="_blank" href="https://users.nepsechart.com/policy/termsAndConditions.html">Terms and Conditions</a></small>
                  }
                </CardFooter>
              </Card>
            </Col>
          </Row>
        </Container>
      }
    </React.Fragment>
  );
}


export default Register;
